import { useState } from 'react';
import './TwoFactorLogin.css';

const TwoFactorLogin = ({ onVerify, onCancel, username }) => {
    const [code, setCode] = useState('');
    const [useBackupCode, setUseBackupCode] = useState(false);
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(false);

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (code.length < 6) {
            setError('Ge√ßersiz kod uzunluƒüu');
            return;
        }

        setLoading(true);
        setError('');

        try {
            await onVerify(code, useBackupCode);
        } catch (err) {
            setError(err.message || 'Doƒürulama ba≈üarƒ±sƒ±z');
            setCode('');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="twofa-login-overlay">
            <div className="twofa-login-content">
                <button className="twofa-login-close" onClick={onCancel}>√ó</button>

                <h2>üîê ƒ∞ki Fakt√∂rl√º Doƒürulama</h2>
                <p className="twofa-login-subtitle">
                    {username} hesabƒ±na giri≈ü yapƒ±lƒ±yor
                </p>

                <form onSubmit={handleSubmit}>
                    <div className="twofa-login-input-group">
                        <label>
                            {useBackupCode ? 'Yedek Kod' : 'Authenticator Kodu'}
                        </label>
                        <input
                            type="text"
                            value={code}
                            onChange={(e) => setCode(e.target.value.replace(/\D/g, ''))}
                            placeholder={useBackupCode ? "12345678" : "000000"}
                            maxLength={useBackupCode ? 8 : 6}
                            className="twofa-login-input"
                            autoFocus
                            disabled={loading}
                        />
                    </div>

                    {error && (
                        <div className="twofa-login-error">
                            ‚ùå {error}
                        </div>
                    )}

                    <button
                        type="submit"
                        className="twofa-login-submit"
                        disabled={loading || code.length < 6}
                    >
                        {loading ? 'Doƒürulanƒ±yor...' : 'Giri≈ü Yap'}
                    </button>

                    <button
                        type="button"
                        onClick={() => {
                            setUseBackupCode(!useBackupCode);
                            setCode('');
                            setError('');
                        }}
                        className="twofa-login-toggle"
                    >
                        {useBackupCode
                            ? '‚Üê Authenticator Kodu Kullan'
                            : 'Yedek Kod Kullan ‚Üí'}
                    </button>
                </form>

                <div className="twofa-login-help">
                    <p>üí° <strong>ƒ∞pucu:</strong></p>
                    <ul>
                        <li>Google Authenticator veya Authy uygulamasƒ±nƒ± a√ßƒ±n</li>
                        <li>PAWSCORD i√ßin g√∂sterilen 6 haneli kodu girin</li>
                        <li>Telefonunuza eri≈üemiyorsanƒ±z yedek kodlarƒ±nƒ±zƒ± kullanƒ±n</li>
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default TwoFactorLogin;


